/* new nav css */
@font-face {
  font-family: 'FH';
  src: url('../Icon/font/custom.woff') format('truetype');
}

@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700&display=swap');

* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

.navbar{
  width: 100%;
  margin-right: -1rem;
}

.navbar-brand {
  font-size: 1.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.brand-name {
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 100;
  margin-left: -20px;
  font-family: 'FH';
  text-decoration: none;
}

.dropdown-menu {
  white-space: normal;
}

.brand-logo img {
  height: 40px;
}

nav {
  background: #131313ea;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  height: 70px;
  padding: 0 100px;
  background-image: url('../Icon/silhouette/silhouette.png');
  background-size: 15rem;
}

.dropdown-menu {
  background-color: #fff;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  padding: 2px;
  margin-left: -0.3rem;
  text-align: center;
}

#dropdown-basic {
  background-color: #3d3d3d9c;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-decoration: none;
}

#dropdown-basic p{
  color: #fff;
}

#dropdown-basic:hover {
  box-shadow: 0px 10px 20px 0px rgba(109, 109, 109, 0.5);
  cursor: pointer;
  text-decoration: none;
}

#dropdown-basic i {
  display: none;
}

nav .nav-items {
  display: flex;
  flex: 1;
  padding: 0 0 0 40px;
}

nav .nav-items li {
  list-style: none;
  padding: 0 15px;
}

nav .nav-items li a {
  font-size: 18px;
  text-transform: uppercase;
  color: #ffffff;
  font-weight: 900;
  position: relative;
  cursor: pointer;
}

nav .nav-items li a:hover {
  color: #ff014fff;
}

nav .nav-items li a::after {
  content: '';
  height: 3px;
  width: 0px;
  background-color: #5000e6;
  position: absolute;
  bottom: 0;
  left: 0;
  transition: 0.3s ease;
}

nav .nav-items li a:hover::after {
  width: 2rem;
}

nav form {
  display: flex;
  height: 40px;
  padding: 2px;
  background: #1e232b;
  width: 15rem;
  border-radius: 0.5rem;
  border: 1px solid rgba(155, 155, 155, 0.2);
}

nav form .search-data {
  width: 100%;
  height: 100%;
  padding: 0 10px;
  color: #fff;
  font-size: 17px;
  border: none;
  font-weight: 500;
  background: none;
  border-radius: 0.5rem;
}

nav form button {
  padding: 0 15px;
  color: #fff;
  font-size: 17px;
  background: transparent;
  border: none;
  cursor: pointer;
}

nav form button:hover {
  background: transparent;
}

nav .menu-icon,
nav .cancel-icon,
nav .search-icon {
  color: #ffffff;
  width: 40px;
  text-align: center;
  font-size: 18px;
  cursor: pointer;
  display: none;
}

nav .menu-icon span,
nav .cancel-icon,
nav .search-icon {
  color: #ffffff;
  display: none;
}

.nav-items-case li {
  display: flex;
  flex-direction: row;
}

.btn-primary {
  background-color: transparent;
  border: none;
  border-radius: 1rem;
  padding: 10px;
  color: #ffffff;
  cursor: pointer;
}

select {
  text-align: center;
  align-items: center;
  height: 2.15rem;
}

.btn-primary:hover {
  background-color: #ffffff;
  box-shadow: 0px 0px 5px 2px #5700fa;
}

.login-search{
  display: flex;
  align-items: center; 
  gap: 0.7rem;
}

.loggedIn #dropdown-basic{
  color: #5000e6;
}

.loggedOut #dropdown-basic{
  color: #ff014fff;
}

@media (max-width: 1245px) {
  nav {
    padding: 0px;
  }

  nav .logo {
    flex: 2;
    text-align: center;
  }

  nav .nav-items {
    position: fixed;
    z-index: 99;
    top: 70px;
    width: 100%;
    left: -100%;
    height: 100%;
    padding: 10px 50px 0 50px;
    text-align: center;
    background: #14181f;
    display: inline-block;
    transition: left 0.3s ease;
  }

  nav .nav-items.active {
    left: 0px;
  }

  nav .nav-items li {
    line-height: 40px;
    margin: 30px 0;
  }

  nav .nav-items li a {
    font-size: 20px;
  }

  nav form {
    position: absolute;
    top: 80px;
    right: 150px;
    opacity: 0;
    pointer-events: none;
    transition: top 0.3s ease, opacity 0.1s ease;
  }

  nav form.active {
    top: 95px;
    opacity: 1;
    pointer-events: auto;
  }

  nav form:before {
    position: absolute;
    content: "";
    top: -13px;
    right: 0px;
    width: 0;
    height: 0;
    z-index: -1;
    border: 10px solid transparent;
    border-bottom-color: #1e232b;
    margin: -20px 0 0;
  }

  nav form:after {
    position: absolute;
    content: '';
    height: 60px;
    padding: 2px;
    background: #1e232b;
    border-radius: 0.5rem;
    min-width: calc(100% + 20px);
    z-index: -2;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  nav .menu-icon {
    display: block;
  }

  nav .search-icon,
  nav .menu-icon span {
    color: #ffffff;
    display: block;
  }

  nav .menu-icon span.hide,
  nav .search-icon.hide {
    color: #ffffff;
    display: none;
  }

  nav .cancel-icon.show {
    color: #ffffff;
    display: block;
  }
}

nav .logo.space {
  color: red;
  padding: 0 5px 0 0;
}

@media (max-width: 980px) {
  nav .menu-icon,
  nav .cancel-icon,
  nav .search-icon {
    color: #ffffff;
    margin: 0 20px;
  }

  nav form {
    right: 30px;
  }
}

nav .logo.space {
  color: red;
  padding: 0 5px 0 0;
}

@media (max-width: 500px) {

  #dropdown-basic p {
    display: none;
  }  

  #dropdown-basic i {
    display: block;
  }  
}

@media (max-width: 410px) {

  .login-search{ 
    margin-left: 12rem;
  }

  .logo{
    position: absolute;
    margin-left: 20px;
  }

  .menu-icon{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}

@media (max-width: 410px) {

  .login-search{ 
    margin-left: 11rem;
  }
}

@media (max-width: 350px) {
  nav .menu-icon,
  nav .cancel-icon,
  nav .search-icon {
    color: #ffffff;
    font-size: 16px;
  }
}

.content {
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  transform: translate(-50%, -50%);
}

.content header {
  font-size: 30px;
  font-weight: 700;
}

.content .text {
  font-size: 30px;
  font-weight: 700;
}

.content .space {
  margin: 10px 0;
}
